.mynavbar {
  background-image: linear-gradient(to right, #a116ef, #390999);
  position: relative;
  padding: 10px;
  border-radius: 0px 0px 30px 30px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.navbar_logo {
  width: 300px;
  min-width: 150px;
  height: auto;
  line-height: 60px;
  padding: 0 10px;
  box-sizing: border-box;
  float: left;
}

nav {
  float: right;
  position: relative;
  min-width: 350px;
  display: flex;
  margin: 3rem 4rem;
  /* height: 50px; */
  font-size: 0;
}

.clearfix {
  clear: both;
}

.menu-toggle {
  display: none;
}

nav a {
  font-size: 18px;
  color: white;
  display: block;
  text-decoration: none;
  position: relative;
  z-index: 21;
  display: flex;
  justify-content: center;
  margin: 1rem 1.5rem;
  font-family: Montserrat;
}
nav a:hover{
  color:white !important
}
nav .animation {
  position: absolute;
  height: 5px;
  bottom: 10px;
  z-index: 0;
  background: gold;
  border-radius: 8px;
  transition: all 0.7s ease 0s;
}

a:nth-child(1) {
  width: 100px;
}
nav .start-home,
a:nth-child(1):hover ~ .animation {
  width: 60px;
  left: 20px;
}
a:nth-child(2) {
  width: 130px;
}
nav .start-user,
a:nth-child(2):hover ~ .animation {
  width: 176px;
  left: 89px;
}
a:nth-child(3) {
  width: 100px;
}
nav .start-hospital,
a:nth-child(3):hover ~ .animation {
  width: 90px;
  left: 280px;
}
a:nth-child(4) {
  width: 100px;
}
nav .start-aboutus,
a:nth-child(4):hover ~ .animation {
  width: 74px;
  left: 394px;
}
a:nth-child(5) {
  width: 100px;
}
nav .start-collaborate,
a:nth-child(5):hover ~ .animation {
  width: 64px;
  left: 495px;
}
