@keyframes fadeInAnimation32 {
  from {
    opacity: 0;
    height: 0;
    top: 40%;
    width: 0;
    left: 50%;
  }
  to {
    opacity: 1;
    height: 40%;
    top: 20%;
    width: 80%;
    left: 10%;
  }
}
@keyframes fadeInAnimation {
  from { 
    opacity: 0;
    height: 10px;
  }
  to   { 
    opacity: 1; 
    height: 210px;
  }
}

@keyframes fadeOutAnimation {
  from { 
    opacity: 1; 
    height: 210px;
  }
  to   { 
    opacity: 0;
    height: 10px;
  }
}

@media screen and (max-width: 1260px) {
  .menu-toggle {
    display: block;
    width: 40px;
    height: 40px;
    margin: 40px 20px;
    float: right;
    cursor: pointer;
    text-align: center;
    font-size: 30px;
    transition: all 1s ease;
  }
  nav {
    display: none;
  }
  .navbar_logo {
    min-width: 250px;
    width: 70vw;
    max-width: 300px;
  }
  nav.mid {
    display: block;
    width: 100%;
    margin: 0 auto;
    align-items: center;
    text-align: center;
    animation: fadeOutAnimation 1s;
  }
  nav.mid div {
    display: none;
  }
  nav.active div {
    display: none;
  }
  nav.active {
    display: block;
    width: 100%;
    margin: 0;
    align-items: center;
    text-align: center;
    animation: fadeInAnimation 1s;
  }
  nav.active a,
  nav.mid a {
    padding: 10px 0;
    margin: 0 auto;
  }
  nav.active a:hover {
    border-bottom: solid gold 5px;
    border-radius: 5px;
    transition: all 0.5s ease;
    color:white
  }
}
